body {
    background-color: rgb(250, 250, 250);
    background-size: cover;
    background-position: center;
    background-image: url("https://images.unsplash.com/photo-1586001549152-62a311658c88?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=335&q=80");
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.headerText {
    font-size: 50px;
    font-family: Arial, Helvetica, sans-serif;
}

@media (max-width: 770px) {
    .container {
        margin: 0 10%;
    }
}